import React from 'react';
import Image_wth_txt from "../components/UI/image_wth_txt/Image_wth_txt"
import Header_space from "../components/UI/image_wth_txt/Header_space"
import Fobos from  "../images/Fobos.jpg";
import Therad100 from  "../images/Therad100.jpg";
import Therad200 from  "../images/Therad200.jpg";
import Therad300 from  "../images/Therad300.jpg";
import Test_platform from  "../images/Test_platform.jpg";
import classes from './MyProjects.module.css';


function MyProjects() {
  return (
    <div>
      <Header_space>
            Fobos XR
            <Image_wth_txt src={Fobos} alt={"img"}>
            Stationary diagnostic digital radiological forensic system.
            </Image_wth_txt>
          </Header_space>

          <Header_space>
          Therad 100
            <Image_wth_txt src={Therad100} alt={"img"}>
            Medical X-ray system for the effective treatment of cancer, dermatological diseases and inflammation.
            </Image_wth_txt>
          </Header_space>

          <Header_space>
          Therad 200
            <Image_wth_txt src={Therad200} alt={"img"}>
            Medical X-ray system for the effective treatment of cancer, dermatological diseases and inflammation.
            </Image_wth_txt>
          </Header_space>

          <Header_space>
            Therad 300
            <Image_wth_txt src={Therad300} alt={"img"}>
            Medical X-ray system for the effective treatment of cancer, dermatological diseases and inflammation.
            </Image_wth_txt>
          </Header_space>

          <Header_space>
            Test platfom for DTP systems
            <Image_wth_txt src={Test_platform} alt={"img"}>
            Intelligent Moving Platform with web browser control
            </Image_wth_txt>
          </Header_space>

          <Header_space>
            My mini JS game
            <Image_wth_txt>
              <div className={classes.link_play_game_space}>
                <a  href="https://vladzalehay.github.io/jetpackGames/" className={classes.link_play_game}>Play Game</a>
              </div>
            </Image_wth_txt>
          </Header_space>
    </div>
  );
}

export default MyProjects;